body {
  background: linear-gradient(#F8E0F7, #FBEFFB);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}


.sideTree {
  width: 20%;
  max-width: 300px;
}

.main_topic {
  text-align: center;
}

.mainTree {
  width: 100%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.login {
  margin-bottom: 10px;
  text-align: right;
}

.btn_open {
  text-align: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
}

.btn_open button {
  border-radius: 40px;
  border: 1px pink solid;
  color: #F5A9F2;
}

.postFrom {
  width: 100px;
  margin-left: 153px;
}

.postWrite {
  font-size: 20px;
  padding-bottom: 5px;
}

.postWrite_detail {
  margin-right: 10px;
  width: 200px;
  height: 70px;
  margin-left: 10px;

}

.postWrite_main {
  max-width: 350px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
}

.postSend {
  margin-left: 220px;
  margin-top: 10px;
}

.treeName {
  text-align: center;
  font-size: 25px;
}

.mainPost {
  font-size: 15px;
  border-radius: 30px;
  border: solid 1px white;
  padding-left: 5%;
  margin-bottom: 10px;
  background-color: #F8E0F7;
}